// usePageTracking.js
import { useEffect } from "react";
import ReactGA from "react-ga4"; // Use react-ga4 para GA4


const usePageTracking = () => {
  useEffect(() => {
    // Inicialize o Google Analytics
    ReactGA.initialize("G-471809705"); // Substitua pelo seu Measurement ID

    // Escute mudanas de rota e registre as visualizaes de pgina
    const trackPage = () => {
      ReactGA.send({
        hitType: "pageview",
        page: window.location.pathname + window.location.search,
      });
    };

    trackPage(); // Registre a pgina inicial

    window.addEventListener("popstate", trackPage); // Registre ao navegar com os botes do navegador

    return () => {
      window.removeEventListener("popstate", trackPage); // Limpeza
    };
  }, []);
};

export default usePageTracking;



