import React from "react";

const ListaNoticias = () => {
  const noticias = [
    {
      "Nome": "LotoFcil premiou 3 apostas",
      "Link": "https://einvestidor.estadao.com.br/loterias/lotofacil-resultado-concurso-3286-premio/",
      imgSrc: require("../../images/noticias/lotofacil1.webp"),
      "Texto": "As apostas vencedoras so das seguintes cidades: Anage (BA), Caarapo (MS) e Itaquaquecetuba (SP). Nmeros sorteadaos: 01-04-05-06-07-08-11-13-14-18-20-21-23-24-25."
    },
    {
      "Nome": "Mega da Virada 2024",
      "Link": "https://bnldata.com.br/",
      imgSrc: require("../../images/noticias/megavirada.webp"),
      "Texto": "A Mega da Virada de 2024, com prmio de R$ 635 milhes, emocionou os brasileiros. O sorteio de 31 de dezembro contou com nmeros como 01, 17, e 50. Milhes de apostas foram feitas para tentar conquistar o prmio milionrio."
    },
    {
      "Nome": "Prmios No Resgatados",
      "Link": "https://g1.globo.com/loterias/noticia/2024/12/31/brasileiros-deixaram-de-sacar-mais-de-r-380-milhoes-em-premios-de-loterias-em-2024-diz-caixa.ghtml",
      imgSrc: require("../../images/noticias/megaresultdo.webp"),
      "Texto": "Em 2024, mais de R$ 380 milhes em prmios de loterias no foram resgatados. Esse montante ser destinado ao Fundo de Financiamento Estudantil (Fies). A quantidade de prmios no retirados apresentou uma leve queda em comparao ao ano anterior."
    },
    {
      "Nome": "Mega-Sena Acumulada",
      "Link": "https://g1.globo.com/loterias/",
      imgSrc: require("../../images/noticias/mega3.webp"),
      "Texto": "O sorteio 2.811 da Mega-Sena, realizado em dezembro, terminou sem ganhadores. O prmio acumulou em R$ 8,5 milhes e a expectativa para o prximo concurso  grande. Apostadores esperam que o prmio seja finalmente conquistado em breve."
    },
    {
      "Nome": "Resultado da Lotofcil",
      "Link": "https://g1.globo.com/loterias/",
      imgSrc: require("../../images/noticias/loto.webp"),
      "Texto": "O concurso 3285 da Lotofcil premiou quatro apostas vencedoras. Cada uma delas levar uma parte do prmio, estimado em R$ 1,7 milho. A Lotofcil continua sendo uma das preferidas pelos apostadores brasileiros."
    },
    {
      "Nome": "Prmio da Timemania Acumulado",
      "Link": "https://noticias.uol.com.br/loterias/ultimas/",
      imgSrc: require("../../images/noticias/time.webp"),
      "Texto": "A Timemania acumulou e o prmio j chega a R$ 2 milhes. Nenhum apostador conseguiu acertar todos os nmeros, e agora a expectativa para o prximo sorteio  ainda maior. A competio pelo prmio tem atrado cada vez mais participantes."
    }
  ];

  return (
    <div
      style={{
        width: "90%",
        maxWidth: "600px", // Limita a largura mxima
        margin: "0px auto", // Centraliza o componente horizontalmente
        padding: "10px",
        backgroundColor: "#f9f9f9",
        borderRadius: "10px",
        boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
        maxHeight: "860px", // Limita a altura do continer
        overflowY: "auto", // Habilita o scroll quando o contedo exceder a altura

      }}
    >
      <p
        style={{
          fontWeight: "bold",
          fontSize: "1.2rem",
          color: "#333",
          marginBottom: "20px",
          textAlign: "center"
        }}
      >
        ltimas Notcias sobre Loterias no Brasil:
      </p>

      {noticias.map((noticia, index) => (
        <div
          key={index}
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            marginBottom: "20px" // Espaamento consistente entre os itens
          }}
        >
          <a href={noticia.Link} target="_blank" rel="noopener noreferrer">
            <img
              src={noticia.imgSrc}
              alt={noticia.Nome}
              style={{
                width: '260px',
                height: '120px', // Proporo mantida
                objectFit: "cover",
                borderRadius: "10px",
                marginBottom: "10px", // Espao reduzido entre a imagem e o texto
                justifySelf:"center",
                alignSelf:"center",
              }}
            />
          </a>
          <p
            style={{
              fontWeight: "bold",
              color: "#222",
              textAlign: "center", // Centraliza o nome
              margin: "10px 0 5px" // Espaamento entre nome e texto
            }}
          >
            {noticia.Nome}
          </p>
          <p
            style={{
              color: "#555",
              lineHeight: "1.6",
              textAlign: "justify", // Justificado
              margin: 0, // Remove margens adicionais
              padding: "0 10px" // Pequeno padding lateral para melhorar leitura
            }}
          >
            {noticia.Texto}
          </p>
        </div>
      ))}
    </div>
  );
};

export default ListaNoticias;
